@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
form {
  display: flex;
  flex-direction: column;
}

.form-area {
  width: auto;
  margin: 8px 16px;
  color: var(--mainBlue);
  font-family: 'Roboto', sans-serif;
  border: 1px solid var(--mainBlue);
}

input {
  color: var(--mainBlue);
  background-color: var(--mainWhite);
  border: 1px solid var(--mainBlue);
  border-radius: 5px;
  margin: 8px 16px;
  padding: 8px;
  font-style: italic;
}

textarea {
  min-height: 50px;
  color: var(--mainBlue);
  background-color: var(--mainWhite);
  border: 1px solid var(--mainBlue);
  border-radius: 5px;
  margin: 8px 16px;
  padding: 8px;
  font-style: italic;
}

.add-button {
  padding: 4px 16px;
  width: 75px;
  border: 0;
  background-color: var(--mainBlue);
  color: var(--mainWhite);
  cursor: pointer;
}

.delete-button {
  padding: 4px;
  width: 75px;
  border: 1px solid var(--mainBlue);
  background-color: var(--mainWhite);
  color: var(--mainBlue);
  cursor: pointer;
}

.input-header {
  font-size: 24px;
  font-weight: 400;
  margin: 8px 16px;
}

.button-container {
  margin: 0 16px 8px 16px;
  display: flex;
  justify-content: right;
  align-self: right;
}

.add-button-container {
  margin: 0 16px 8px 16px;
  display: flex;
  justify-content: right;
  align-self: right;
}

.document-area {
  width: auto;
  margin: 8px 16px;
  padding: 16px;
  color: var(--mainBlue);
  font-family: 'Roboto', sans-serif;
  border: 1px solid var(--mainBlue);
  font-size: 12px;
}

.general-name {
  font-weight: bold;
  font-size: 32px;
  margin: 0;
}

.general-items {
  font-style: italic;
  font-weight: 300;
  padding-right: 4px;
  margin: 0;
}

.general-items-container {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
}

.general-textarea {
  font-weight: 400;
  margin: 16px 0;
}

.header-show {
  font-weight: 400;
}

.items {
  font-style: italic;
  font-weight: 300;
  padding-right: 4px;
  margin: 0;
}

.items-container {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
}

.education-degree {
  padding-left: 16px;
  margin: 8px 0;
}

.bold-items-container {
  display: flex;
  flex-wrap: wrap;
  margin: 8px 0;
  padding-left: 16px;
}

.bold-items {
  font-weight: 400;
  padding-right: 4px;
  margin: 0;
}

.experience-textarea {
  margin: 8px 0;
  padding-left: 16px;
  font-weight: 300;
}

:root {
  --mainBlue: #0d2f40;
  --mainWhite: #ffffff;
}

.App,
.root,
body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.App {
  display: grid;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100vw;
}

@media only screen and (min-width: 768px) {
  .App {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
  }
}

